.description{
    max-height: 150px;
    overflow: hidden;
    font-size: large;
    max-height: 9ch;
    padding-left: 10px;
}

.description::after{
    position: absolute;
    bottom: 0;  
    height: 100px;
    left: 0;
    right: 0;
    content: "";
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgb(31, 31, 31) 100%);
}