@import url(https://fonts.googleapis.com/css?family=Bellota+Text:300,300i,400&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Bellota Text', cursive;
  color: #000000;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.description{
    max-height: 150px;
    overflow: hidden;
    font-size: large;
    max-height: 9ch;
    padding-left: 10px;
}

.description::after{
    position: absolute;
    bottom: 0;  
    height: 100px;
    left: 0;
    right: 0;
    content: "";
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgb(31, 31, 31) 100%);
}
.customTitle {
    font-size: 72px;
    background: -webkit-linear-gradient(#0CBABA, #0CBABA);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: lighter;
  }
